footer {
  align-items: center;
  gap: 0;
  flex-direction: column;
  color: whitesmoke;

  span {
    gap: 5px;
  }

  .skill-preview {
    img {
      width: 30px;
    }
  }

  @media (max-width: 500px) {
    font-size: 0.7rem;
  }

  .contact-ways {
    .github-icon {
      filter: invert(99%) sepia(93%) saturate(2%) hue-rotate(106deg)
        brightness(113%) contrast(101%);
        margin-top: 5px;
    }

    gap: 3px;
    img {
      height: 20px;
    }

    .gmail-img {
      margin-top: 5px;
    }
  }
}
